.loginsignup {
  width: 100%;
  height: 100vh;
  background: #fce3fe;
  padding-top: 50px;
  margin-bottom: 3rem;
}
.loginsignup-container {
  width: 570px;
  height: 500px;
  background: white;
  margin: auto;
  padding: 40px 60px;

}

.loginsignup-fields {
  display: flex;
  flex-direction: column;
  gap: 29px;
  margin-top: 30px;
}
.loginsignup-fields input {
  height: 52px;
  width: 90%;
  padding-left: 20px;
  border: 1px solid #c9c9c9;
  outline: none;
  font-size: 18px;
}
.loginsignup-container button {
  width: 95%;
  height: 52px;
  color: white;
  background: hwb(98 2% 15%);
  margin-top: 30px;
  border: none;
  font-size: 24px;
  font-weight: 500;
  cursor: pointer;
}
.loginsignup-login{
    margin-top: 20px;
    color: #5c5c5c;
    font-size: 18px;
    font-weight: 500;
}
.loginsignup-login span{
    color: hwb(98 2% 15%);
    font-weight: 600;
}
.loginsignup-agress{
    display: flex;
    align-items: center;
    margin-top: 25px;
    gap: 20px;
    color: #5c5c5c;
    font-size: 18px;
    font-weight: 500;
}