.popular {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  height: 90vh;
}
.popular h1 {
  color: #171717;
  font-size: 40px;
  font-weight: 600;
}
.popular hr {
  width: 250px;
  height: 6px;
  border-radius: 10px;
  background: #252525;
}
.popular-item {
  margin-top: 50px;
  display: flex;
  gap: 15px;
  width: 600px;
  justify-content:center;
  align-items: center;
}
.popular-item img{
    width: 250px;
}
